import { Component, Input, EventEmitter, Output, Directive } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';

import { ServiceAgreement } from '../user-context-menu.service';

export interface UserContextMenuDropdownCustomizableContext {
  currentContext: ServiceAgreement | undefined;
  canSelectContext: boolean;
  showSingleContext: boolean | undefined;
}

@Component({
  selector: 'bb-user-context-menu-dropdown',
  templateUrl: './user-context-menu-dropdown.component.html',
})
export class UserContextMenuDropdownComponent {
  hostRef = this;
  @Input() currentContext: ServiceAgreement | undefined;
  @Input() showSingleContext: boolean | undefined;
  @Input() canSelectContext = false;

  @Output() changeContext = new EventEmitter<void>();
  @Output() logout = new EventEmitter<void>();

  onChangeContext() {
    if (this.canSelectContext) {
      this.changeContext.emit();
    }
  }

  onLogout() {
    this.logout.emit();
  }
}

@Directive({
  selector: '[bbUserContextMenuDropdownCustomizable]',
})
export class UserContextMenuDropdownCustomizableDirective extends BbTemplate<
  UserContextMenuDropdownComponent,
  UserContextMenuDropdownCustomizableContext
> {}
